.container {
  display: flex;
  flex-direction: column;
  width: 340px;
}

.title {
  font-size: 18px;
  text-align: center;
  margin-bottom: 20px;
  @media screen and (min-width: 600px) {
    font-size: 22px;
  }
}

.history {
  display: flex;
  flex-direction: column;
}

.log {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 8px;
}
.text {
  font-size: 14px;
}
