.container {
  flex-direction: column;
  align-items: center;
  @media screen and (min-width: 600px) {
    flex-direction: row;
    justify-content: space-between;
    align-items: initial;
  }
}

.machinesList {
  margin-bottom: 60px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media screen and (min-width: 600px) {
    margin-bottom: 0;
    align-items: flex-start;
  }
}

.listTitle {
  font-size: 22px;
  margin-bottom: 20px;
}

.machine {
  margin-bottom: 15px;
}

.link {
  font-size: 20px;
  text-decoration: underline;
}

.formWrap {
  width: 220px;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 220px;
}

.error {
  margin-top: 20px;
  color: red;
  margin-bottom: 15px;
}
