.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  > * {
    width: 270px;
  }
  @media screen and (min-width: 980px) {
    margin-top: 200px;
    > * {
      width: 350px;
    }
  }
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.title {
  font-size: 26px;
  margin-bottom: 20px;
  text-align: center;
}

.error {
  color: red;
  margin-top: 15px;
}
