.container {
  flex-direction: column;
  @media screen and (min-width: 960px) {
    flex-direction: row;
    justify-content: space-around;
  }
}
.leftBlock {
  display: flex;
  flex-direction: column;

  @media screen and (min-width: 960px) {
    width: 60%;
  }

  @media screen and (min-width: 1280px) {
    width: 750px;
  }
}

.rightBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
}
