.container {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media screen and (min-width: 600px) {
    padding-left: 50px;
    padding-right: 50px;
  }

  @media screen and (min-width: 960px) {
    padding-left: 0;
    padding-right: 0;
  }
}

.title {
  font-size: 18px;
  text-align: center;
  margin-bottom: 20px;
  @media screen and (min-width: 600px) {
    font-size: 22px;
  }
}

.nodesList {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  @media screen and (min-width: 600px) {
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
    justify-content: space-around;
  }
}

.node {
  display: flex;
  flex-direction: column;
  width: 200px;
  align-items: center;
  margin-bottom: 20px;
  margin-left: 20px;
  margin-right: 20px;
}

.nodeTitle {
  font-size: 16px;
  margin-bottom: 10px;
}

.text {
  margin-bottom: 6px;
  font-size: 14px;
}

.warning {
  background-color: yellow;
}

.danger {
  background-color: red;
}

.message {
  width: 200px;
  border-radius: 4px;
  text-align: center;
  padding: 4px;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 200px;
}
